import React, { useState, useMemo } from "react";
import {
  getTruelyEven,
  IncStsType,
  DecStsType,
  OptionType,
  capitalize,
} from "@tanyoknits/utils";
import {
  NumberDecrementStepper,
  NumberIncrementStepper,
  NumberInput,
  NumberInputField,
  NumberInputStepper,
  Switch,
  Button,
  Accordion,
  AccordionItem,
  AccordionButton,
  AccordionIcon,
  Box,
  AccordionPanel,
  Select,
  RadioGroup,
  Stack,
  Radio,
  Text,
  HStack,
  Flex,
  Divider,
} from "@chakra-ui/react";

type Status = "empty" | "nochange" | "invalid" | "valid";

const incStsOptions: { label: string; value: IncStsType }[] = [
  { label: "All generic M1", value: "m1-m1" },
  { label: "Symmetrical (M1R then M1L)", value: "m1r-m1l" },
  { label: "Symmetrical (M1L then M1R)", value: "m1l-m1r" },
];
const dec1StsOptions: { label: string; value: DecStsType }[] = [
  { label: "All k2tog (knit 2 together)", value: "k2tog-k2tog" },
  { label: "All ssk (slip, slip, knit)", value: "ssk-ssk" },
  { label: "Symmetrical (k2tog then ssk)", value: "k2tog-ssk" },
  { label: "Symmetrical (ssk then k2tog)", value: "ssk-k2tog" },
];
const dec2StsOptions: { label: string; value: DecStsType }[] = [
  { label: "All cdd (centered double decrease)", value: "cdd-cdd-k2tog" },
  { label: "All k3tog (knit 3 together)", value: "k3tog-k3tog-k2tog" },
  { label: "All sk2p (left double decrease)", value: "sk2p-sk2p-ssk" },
  { label: "Symmetrical (k3tog then sk2p)", value: "k3tog-sk2p-ssk" },
  { label: "Symmetrical (sk2p then k3tog)", value: "sk2p-k3tog-k2tog" },
];

const stsTypeOptions = [incStsOptions, dec1StsOptions, dec2StsOptions];
const defaultOption: OptionType = {
  avoidEnds: false,
  sts: "m1-m1",
  count: 0,
  hasDoubleNest: true,
};

const optionStyle = { bg: "gray.600", color: "white" };

export default function Calculator() {
  const [startSts, setStartSts] = useState<number>(100);
  const [endSts, setEndSts] = useState<number>(120);
  const [status, setStatus] = useState<Status>("empty");
  const [options, setOptions] = useState<OptionType>(defaultOption);
  const [display, setDisplay] = useState<string>("list");

  const calculate = () => {
    if (endSts === startSts) {
      setStatus("nochange");
    } else if (
      (endSts > startSts && endSts - startSts > startSts) ||
      (endSts < startSts && startSts - endSts > endSts)
    ) {
      setStatus("invalid");
    } else {
      const calOptions =
        startSts > endSts
          ? {
              avoidEnds: false,
              sts: dec1StsOptions[0].value,
              count: 2,
              hasDoubleNest: true,
            }
          : defaultOption;
      setOptions(calOptions);
      setStatus("valid");
    }
  };

  const updateStartSts = (stsCount: number): void => {
    setStatus("empty");
    setStartSts(stsCount);
  };
  const updateEndSts = (stsCount: number): void => {
    setStatus("empty");
    setEndSts(stsCount);
  };

  let changeText = "No change";
  if (endSts > startSts) {
    changeText = "increase";
  } else if (endSts < startSts) {
    changeText = "decrease";
  }

  const resultDisplay: React.ReactNode = useMemo(() => {
    if (status === "nochange") {
      return (
        <Text fontSize="lg" color="tomato" pt={4} fontWeight={600}>
          ! No increase or decrease
        </Text>
      );
    }
    if (status === "invalid") {
      return (
        <Text fontSize="lg" color="tomato" pt={4} fontWeight={600}>
          ! Cannot {changeText} more stitches than the number of{" "}
          {changeText === "increase" ? "start" : "end"} stitch
        </Text>
      );
    }
    const result = getTruelyEven(startSts, endSts, options);
    return (
      <>
        <Accordion allowToggle pt={8} pb={4} width="fit-content">
          <AccordionItem>
            <h2>
              <AccordionButton
                color="gray.600"
                fontWeight={600}
                _hover={optionStyle}
                _expanded={optionStyle}
              >
                <Box flex="1" textAlign="left" pr={2}>
                  Options
                </Box>
                <AccordionIcon />
              </AccordionButton>
            </h2>
            <AccordionPanel padding={4}>
              <Stack spacing={4}>
                <Flex gap={4} alignItems="center">
                  <Text fontWeight={600}>
                    {capitalize(changeText)} stitches
                  </Text>
                  {startSts > endSts && (
                    <Select
                      size="sm"
                      width="fit-content"
                      defaultValue={options.count}
                      onChange={(e) => {
                        setOptions({
                          ...options,
                          count: +e.target.value,
                          sts: stsTypeOptions[+e.target.value][0].value,
                        });
                      }}
                    >
                      <option value="2">1 stitch per decrease</option>
                      <option value="3">2 stitches per decrease</option>
                    </Select>
                  )}
                  <Select
                    size="sm"
                    width="fit-content"
                    defaultValue={options.sts}
                    onChange={(e) =>
                      setOptions({
                        ...options,
                        sts: e.target.value as IncStsType | DecStsType,
                      })
                    }
                  >
                    {stsTypeOptions[options.count].map((option) => (
                      <option key={option.value} value={option.value}>
                        {option.label}
                      </option>
                    ))}
                  </Select>
                </Flex>
                {result?.avoidEndAvailable && (
                  <Flex gap={4} alignItems="center">
                    <Text fontWeight={600}>Avoid {changeText} at the end</Text>
                    <Switch
                      id="isAvoidEndsChecked"
                      isChecked={options.avoidEnds}
                      onChange={(e) =>
                        setOptions({
                          ...options,
                          avoidEnds: e.target.checked,
                        })
                      }
                    />
                  </Flex>
                )}
                <Divider />
                <Flex gap={4} alignItems="center">
                  <Text fontWeight={600}>Display</Text>
                  <RadioGroup onChange={setDisplay} value={display}>
                    <Stack direction="row" gap={2}>
                      <Radio value="list">Line by repeats</Radio>
                      <Radio value="inline">All in one line</Radio>
                    </Stack>
                  </RadioGroup>
                </Flex>
                {result?.doubleNestAvailable && (
                  <Flex gap={4} alignItems="center" fontWeight={600}>
                    <Text>Nest two-level repeats</Text>
                    <Switch
                      id="isNestChecked"
                      isChecked={options.hasDoubleNest}
                      onChange={(e) =>
                        setOptions({
                          ...options,
                          hasDoubleNest: e.target.checked,
                        })
                      }
                    />
                  </Flex>
                )}
              </Stack>
            </AccordionPanel>
          </AccordionItem>
        </Accordion>
        <Box>
          <Text fontSize="xl" fontWeight={700}>
            {capitalize(changeText)}
            {` `}
            {Math.abs(endSts - startSts)} sts
          </Text>
          {display === "inline" ? (
            <Text fontSize="lg">{result.sts.join(", ")}</Text>
          ) : (
            <div>
              {result.sts.map((s, i) => (
                <Text key={i} fontSize="lg">
                  {s}
                </Text>
              ))}
            </div>
          )}
        </Box>
      </>
    );
  }, [startSts, endSts, status, display, options, changeText]);

  return (
    <div>
      <HStack pb={4}>
        <Text fontSize="lg" w={40} fontWeight={600}>
          Start stitch count
        </Text>
        <NumberInput
          size="md"
          maxW={24}
          defaultValue={startSts.toString()}
          min={1}
          onChange={(value) => updateStartSts(+value)}
        >
          <NumberInputField />
          <NumberInputStepper>
            <NumberIncrementStepper />
            <NumberDecrementStepper />
          </NumberInputStepper>
        </NumberInput>
      </HStack>
      <HStack pb={4}>
        <Text fontSize="lg" w={40} fontWeight={600}>
          End stitch count
        </Text>
        <NumberInput
          size="md"
          maxW={24}
          defaultValue={endSts.toString()}
          min={1}
          onChange={(value) => updateEndSts(+value)}
        >
          <NumberInputField />
          <NumberInputStepper>
            <NumberIncrementStepper />
            <NumberDecrementStepper />
          </NumberInputStepper>
        </NumberInput>
      </HStack>
      <Button onClick={calculate} size="lg">
        Calculate
      </Button>
      {status !== "empty" && resultDisplay}
    </div>
  );
}
